import uniStatusBar from "../../components/uni-status-bar/uni-status-bar.vue";
import otherLogin from "../../components/home/other-login.vue";
export default {
	components:{
		uniStatusBar,
		otherLogin
	},
	data() {
		return {
			status:false,//false代表账号密码登录，true代表手机验证码登录
			disabled:true,
			loading:false,
			username:"",
			password:"",
			phone:"",
			checknum:"",
			codetime:0,
		}
	},
	watch:{
		username(val){
			this.OnBtnChange();
		},
		password(val){
			this.OnBtnChange();
		},
		phone(val){
			this.OnBtnChange();
		},
		checknum(val){
			this.OnBtnChange();
		}
	},
	methods: {
		// 验证手机号码
		isPhone(){
			let mPattern = /^1[34578]\d{9}$/; 
			return mPattern.test(this.phone);
		},
		// 获取验证码
		async getCheckNum(){
			if(this.codetime > 0)return;
			// 验证手机号合法性
			if(!this.isPhone()){
				return uni.showToast({ title: '请输入正确的手机号码', icon:"none" });
			}
			// 请求服务器，发送验证码
			let [err,res] = await this.$http.post('/user/sendcode',{
				phone:this.phone
			});
			// 请求失败处理
			this.$http.errorCheck(err,res);
			if (res.data.errorCode === 30001) return;
			// 发送成功，开启倒计时
			this.codetime=60;
			let timer=setInterval(()=>{
				this.codetime--;
				if(this.codetime < 1){
					clearInterval(timer);
					this.codetime=0;
				}
			},1000);
		},
		// 初始化表单
		initInput(){
			this.username='';
			this.password='';
			this.phone='';
			this.checknum='';
		},
		// 改变按钮状态
		OnBtnChange(){
			if( (this.username && this.password)||(this.phone && this.checknum) ){
				this.disabled=false; return;
			}
			this.disabled=true;
		},
		// 切换登录状态
		changeStatus(){
			this.initInput();
			this.status = !this.status;
		},
		// 返回上一步
		back(){
			uni.navigateBack({ delta: 1 });
		},
		// 提交登录
		submit(){
			// 账号密码登录
			if(!this.status){
				return this.User.login({
					url:"/user/login",
					data:{
						username:this.username,
						password:this.password
					}
				})
			}
			// 验证码登录
			// 验证手机号合法性
			if(!this.isPhone()){
				return uni.showToast({ title: '请输入正确的手机号码', icon:"none" });
			}
			return this.User.login({
				url:"/user/phonelogin",
				data:{
					phone:this.phone,
					code:this.checknum
				}
			});
		}
	}
}
